/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

::-webkit-scrollbar {
  display: none;
}

body {
  font-size: 1.6rem;
  line-height: 1.8;
  background: #002222;
  margin: 0;
}

p {
  font-size: 1.2rem;
}

.container {
  position: relative;
  width: 100%;
  max-width: 160rem;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

a {
  font-family: "Bebas Neue", cursive;
  font-weight: 700;
  text-decoration: none;
  text-transform: uppercase;
}

h1,
h2,
h3,
h4 {
  font-family: "Lato", sans-serif;
  margin: 10em;
  color: black;
  margin: 0;
  font-weight: 500;
}

h1 {
  font-size: 3.8rem;
}

h2 {
  font-size: 3.4rem;
}

h3 {
  font-size: 2.8rem;
  color: rgb(135, 150, 22);
}

h4 {
  font-size: 2rem;
  margin: 0;
}

.bg-img {
  max-width: 100%;
  height: 100vh;
}

.item-unvisible {
  visibility: hidden;
}
@media (min-width: 1007px) {
  .item-unvisible {
    visibility: visible;
  }
}

.item-visible {
  visibility: visible;
}

header {
  width: 100vw;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 101;
  overflow: hidden;
}

header .container {
  max-width: 100vw;
}

.navbar-logo {
  animation: navbar-logo 2s cubic-bezier(0.5, 0.6, 0.6, 1);
  animation-fill-mode: backwards;
}
.navbar-logo h1 {
  font-family: "Bebas Neue", cursive;
  font-size: 5rem;
  color: #7f1a35;
  transition: cubic-bezier(0.8, 1, 0.8, 1) 1s;
}
.navbar-logo h1 span {
  color: #FFF0D3;
}
.navbar-logo h2 {
  padding-left: 1.25rem;
  margin-top: -4rem;
  text-align: center;
  font-family: "Bebas Neue", cursive;
  font-size: 2.5rem;
  color: #7f1a35;
  transition: cubic-bezier(0.8, 1, 0.8, 1) 1s;
}
.navbar-logo h2 span {
  color: #FFF0D3;
}
@media (min-width: 1007px) {
  .navbar-logo h1 {
    font-size: 10rem;
    transition: cubic-bezier(0.8, 1, 0.8, 1) 1s;
  }
  .navbar-logo h1 span {
    color: #FFF0D3;
  }
  .navbar-logo h2 {
    padding-left: 2.5rem;
    margin-top: -8rem;
    text-align: center;
    font-family: "Bebas Neue", cursive;
    font-size: 5rem;
    color: #7f1a35;
    transition: cubic-bezier(0.8, 1, 0.8, 1) 1s;
  }
  .navbar-logo h2 span {
    color: #FFF0D3;
  }
}

@keyframes navbar-logo {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
.checkbox {
  position: absolute;
  display: block;
  height: 32px;
  width: 32px;
  top: 12vh;
  left: 45vw;
  z-index: 5;
  opacity: 0;
  cursor: pointer;
}
@media (min-width: 1007px) {
  .checkbox {
    display: none;
  }
}

.hamburger-lines {
  display: block;
  height: 26px;
  width: 32px;
  margin: 1rem 0;
  z-index: 2;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
@media (min-width: 1007px) {
  .hamburger-lines {
    display: none;
  }
}

.line {
  display: block;
  height: 4px;
  width: 100%;
  border-radius: 10px;
  background: #dfe1e2;
}
@media (min-width: 1007px) {
  .line {
    display: none;
  }
}

.line1 {
  transform-origin: 0% 0%;
  transition: transform 0.4s ease-in-out;
}

.line2 {
  transition: transform 0.2s ease-in-out;
}

.line3 {
  transform-origin: 0% 100%;
  transition: transform 0.4s ease-in-out;
}

.navbar-menu {
  display: none;
  padding-top: 120px;
  background-color: #002222;
  height: 100vh;
  width: 100%;
  margin-top: 1rem;
}
.navbar-menu a {
  color: #FFF0D3;
  padding: 0rem 2rem;
  font-size: 3rem;
}
@media (min-width: 1007px) {
  .navbar-menu {
    padding: 0;
    box-shadow: none;
    display: flex;
    position: fixed;
    height: initial;
    margin: 0;
    transition: none;
    transform: none;
    top: 85vh;
    flex-direction: row;
    justify-content: center;
    background-color: unset;
    transition: cubic-bezier(0.8, 1, 0.8, 1) 0.35s;
  }
  .navbar-menu a {
    padding: 0.45rem 2.5rem 0 2.5rem;
    font-size: 5vw;
    transition: cubic-bezier(0.8, 1, 0.8, 1) 0.8s;
  }
  .navbar-menu .active {
    background-color: #005555;
  }
  .navbar-menu a:hover {
    background-color: rgba(0, 0, 0, 0.6);
  }
}

@keyframes navbar-menu {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
input[type=checkbox]:checked ~ .navbar-menu {
  display: flex;
  flex-direction: column;
  text-align: center;
  animation: navbar-menu-hide 2s cubic-bezier(1, 0.6, 0.6, 0.5);
}

input[type=checkbox]:checked ~ .hamburger-lines .line1 {
  transform: rotate(45deg);
}

input[type=checkbox]:checked ~ .hamburger-lines .line2 {
  transform: scaleY(0);
}

input[type=checkbox]:checked ~ .hamburger-lines .line3 {
  transform: rotate(-45deg);
}

@media (min-width: 1007px) {
  .move-header {
    padding: 0 !important;
  }
}

@media (min-width: 1007px) {
  .bg-header {
    background-color: #002222;
  }
}

.move-logo h1 {
  font-size: 3.8rem;
  transition: cubic-bezier(0.5, 0.3, 0.3, 0.8) 1.4s;
}
.move-logo h2 {
  padding-left: 1.15rem;
  margin-top: -3rem;
  font-size: 1.7rem;
  transition: cubic-bezier(0.5, 0.3, 0.3, 0.8) 1.4s;
}
@media (min-width: 1007px) {
  .move-logo h1 {
    font-size: 5rem;
    transition: cubic-bezier(0.5, 0.3, 0.3, 0.8) 1.4s;
  }
  .move-logo h2 {
    padding-left: 1.13rem;
    margin-top: -4rem;
    font-size: 2.5rem;
    transition: cubic-bezier(0.5, 0.3, 0.3, 0.8) 1.4s;
  }
}

@media (min-width: 1007px) {
  .move-menu {
    top: 90%;
    transition: cubic-bezier(0.8, 1, 0.8, 1) 1.5s;
  }
  .move-menu a {
    font-size: 2vw;
  }
}

@media (min-width: 1007px) {
  .change-color a {
    color: #7f1a35;
  }
  .change-color a:hover {
    background-color: rgba(45, 0, 0, 0.8);
    color: #FFF0D3;
  }
  .change-color .active {
    color: #FFF0D3;
    background-color: #7f1a35;
  }
}

section {
  width: 100%;
  min-height: 100vh;
  overflow: hidden;
}

.personal-img {
  z-index: 100;
  position: fixed;
  top: 15%;
  left: 10%;
  right: 0;
  bottom: 0;
  background-image: url(../../../src/img/test2.png);
  background-size: cover;
  background-repeat: no-repeat;
  animation: personal-img 1.2s cubic-bezier(0.5, 0.6, 0.6, 1);
  animation-fill-mode: backwards;
}

@keyframes personal-img {
  0% {
    opacity: 0;
    transform: scale(1.1);
  }
  100% {
    opacity: 1;
    transform: scale(1);
  }
}
.skills {
  width: 100%;
  height: 100vh;
  background-color: #FFF0D3;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.skills h3 {
  font-size: 20rem;
  color: black;
}

.contact {
  width: 100%;
  height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.contact h3 {
  font-size: 20rem;
  color: black;
}

.move-img {
  position: absolute;
}

#about {
  background-color: #FFF0D3;
}

#about .container {
  margin-top: 8.5rem;
  height: 100vh;
  overflow: hidden;
}
@media (min-width: 1007px) {
  #about .container {
    margin-top: 0;
    flex-direction: row;
    align-items: center;
  }
}

.about-text {
  display: flex;
  flex-direction: column;
  justify-content: center;
  width: 85%;
  height: 65%;
  text-align: justify;
  overflow: hidden;
}
.about-text h4 {
  font-size: 100%;
}
@media (min-width: 1007px) {
  .about-text {
    max-width: 55%;
    height: 50%;
    margin: 0.5rem 1.5rem;
    margin-right: 3rem;
    padding: 0 5rem;
    text-align: justify;
    border-radius: 0.6rem;
  }
  .about-text h4 {
    font-size: 100%;
  }
}

.about-graph {
  display: flex;
  width: 100%;
  height: 35%;
  background-image: url(../../../src/img/about2.png);
  background-position: center;
  background-repeat: no-repeat;
  background-size: contain;
}
@media (min-width: 1007px) {
  .about-graph {
    justify-content: center;
    width: 50%;
    margin: 0.5rem 1rem;
    padding: 0 2rem;
    overflow: hidden;
  }
}

.work {
  width: 100%;
  height: 100vh;
  background-color: #FFF0D3;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.work h3 {
  font-size: 20rem;
  color: black;
}

.swiper {
  width: 100%;
  height: 100%;
}

.swiper-slide {
  text-align: center;
  font-size: 18px;
  background: #fff;
  /* Center slide text vertically */
  display: -webkit-box;
  display: -ms-flexbox;
  display: -webkit-flex;
  display: flex;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  -webkit-justify-content: center;
  justify-content: center;
  -webkit-box-align: center;
  -ms-flex-align: center;
  -webkit-align-items: center;
  align-items: center;
}

.swiper-slide img {
  display: block;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.swiper {
  margin-left: auto;
  margin-right: auto;
}